// aqui eu estou definindo que está página vai ser visisvel para o cliente
'use client'

// Aqui eu estou importando o useState, useEffect e o useRef da biblioteca do React
import { useState, useEffect, useRef } from "react";

// Aqui eu estou exportando de forma padrão a função home
export default function Home() {
  // Criei uma variavel constante que utiliza o useState para criar uma caixa (contador) e o cara responsavel por modificar o valor contido dentro da caixa de acordo com as condições que vão ser pré definidas, o useState define que o contador vai começar com o valor 0 
  const [contador, setContador] = useState(0)

  // A função vai retornar 
  // uma div, um h1 com a variavel contador dentro 
  return (
    <div>
    <h1>{contador}</h1>
    {/*Quando o usuário clicar no botão Adicionar o setContador será executado, ele vai somar o valor dentro do contador + 1 todas vez q o botão for clicado.*/}
    <button onClick={() => setContador(contador + 1)}>Adicionar</button>
    {/*aqui vai ser a mesma coisa a diferença é que o setContador vai zerar tudo quando o botão for clicado*/}
    <button onClick={() => setContador(0)}>Zerar</button>
  </div>
  );
}
